import React from 'react';
import Latex from 'react-latex';
export default function Aderencia(){ return(<>

<h2>
    Teste de Aderência
</h2>

O objetivo é testar se a frequência observada de cada k categoria de uma amostra de n indivíduos obedece um modelo proposto.

<Latex displayMode={true}>{'$$'+
'\\begin{array}{c|c|c|c|c}'+
'&A_{1}&\\dots&A_{k}&Total\\\\ '+
'\\hline'+
'\\text{Frequência Observada}&O_{1}&\\dots&O_{k}&n \\\\'+
'\\text{Frequência Proposta}&E_{1}&\\dots&E_{k}&n'+
'\\end{array}'
+'$$'}</Latex>

<h3>
    Hipóteses do Teste
</h3>

<Latex displayMode={true}>{'$$'+
'\\begin{cases} '+
'h_0 : {O}_{i} = {E}_{i} \\;\\forall i\\\\'+
'h_1 : \\exists\\;{O}_{i} \\mathrel{\\char`≠} {E}_{i}'+
'\\end{cases}'
+'$$'}</Latex>

<h3>
    Estatística do Teste
</h3>

<Latex displayMode={true}>{'$$'+
'X='+
'\\sum^{k_{1}}_{i=1}\\frac{(O_{i}-E_{i})^{2}}{E_{i}}'+
'\\sim \\chi^{2}_{(k-1)}'
+'$$'}</Latex>

</>)}